<template>
  <view class="chat-container">
    <scroll-view
      class="message-scroll-view"
      scroll-y
      :style="{ height: scrollViewHeight + 'px' }"
      :scroll-top="scrollTop"
      scroll-with-animation
      @scrolltoupper="loadMoreMessages"
    >
      <view class="loading-more" v-if="isLoadingMore">正在加载更多...</view>

      <view
        v-for="msg in messages"
        :key="msg._id"
        :id="'msg-' + msg._id"
      >
        <view
          class="message-item"
          :class="[msg.sender === myPhoneNumber ? 'mine' : 'other', { 'system-message': msg.isSystemMessage }, msg.messageType ? msg.messageType : '']"
        >
          <!-- 系统消息图标 -->
          <image
            v-if="msg.isSystemMessage"
            class="system-icon"
            src="/static/images/appointment-time.png"
            mode="aspectFill"
          />
          <!-- 头像（非系统消息显示） -->
          <image
            v-else
            class="avatar"
            :src="msg.avatarUrl"
            mode="aspectFill"
            @error="handleAvatarError(msg)"
          />
          <view class="msg-content">
            <view
              class="bubble"
              :class="{ revoked: msg.isRevoked }"
              @longpress="onMessageLongPress(msg)"
            >
              <text v-if="msg.type === 'text' && !msg.isRevoked">{{ msg.message }}</text>
              <image
                class="msg-image"
                v-if="msg.type === 'image' && !msg.isRevoked"
                :src="msg.fileUrl"
                mode="aspectanud"
                @click="previewImage(msg.fileUrl)"
              />
              <view
                v-if="msg.type === 'location' && !msg.isRevoked"
                class="location-msg"
                @click="previewLocation(msg.location)"
              >
                <image
                  class="map-thumb"
                  :src="msg.location.thumbUrl"
                  mode="aspectFill"
                />
                <view class="map-info">
                  <text class="map-title">{{ msg.location.name || '位置' }}</text>
                  <text class="map-address">{{ msg.location.address }}</text>
                </view>
              </view>
              <text
                class="revoked-text"
                v-if="msg.isRevoked"
              >{{ msg.sender === myPhoneNumber ? '你' : '对方' }}撤回了一条消息</text>
              <!-- 时间戳 -->
              <view class="time">{{ formatTimestamp(msg.timestamp) }}</view>
            </view>
          </view>
        </view>
      </view>
    </scroll-view>

    <view class="input-bar" :style="{ bottom: keyboardHeight + 'px' }">
      <image
        src="/static/images/sendImg.png"
        class="icon-btn"
        @click="sendImage"
      />
      <image
        src="/static/images/location.png"
        class="icon-btn"
        @click="sendLocation"
      />
      <input
        class="input"
        v-model="messageInput"
        placeholder="请输入消息..."
        confirm-type="send"
        @confirm="sendTextMessage"
        :adjust-position="false"
        cursor-spacing="20"
      />
      <button class="send-btn" @click="sendTextMessage">发送</button>
    </view>
  </view>
</template>

<script setup>
import { ref, nextTick, onUnmounted } from 'vue';
import { getCurrentInstance } from 'vue';
import { onLoad, onReady, onShow, onHide } from '@dcloudio/uni-app';

// 常量与配置
const QQ_MAP_KEY_MP   = 'ZOYBZ-S7V6G-OSQQ2-QA54H-2PBHE-CXF46';
const QQ_MAP_KEY_APP  = '3QUBZ-FFCCB-6GYUO-NQI2R-WDUB5-BJFIY';
const DEFAULT_AVATAR  = '/static/images/avatar-placeholder.png';
const PAGE_SIZE       = 15;
const POLL_INTERVAL   = 15000; // App 端轮询间隔 15 秒

// 响应式状态
const sessionId        = ref('');
const myPhoneNumber    = ref('');
const messages         = ref([]);
const messageInput     = ref('');
const scrollTop        = ref(999999);
const scrollViewHeight = ref(0);
const keyboardHeight   = ref(0);
const isLoadingMore    = ref(false);
const noMoreMessages   = ref(false);

// 监听实例与定时器引用
let messageWatcher = null;
let pollingTimer   = null;

// 格式化时间
const formatTimestamp = ts => {
    if (!ts) return '未知时间';
    const d = new Date(ts);
    if (isNaN(d.getTime())) return '无效时间';
    const mm = String(d.getMonth() + 1).padStart(2, '0');
    const dd = String(d.getDate()).padStart(2, '0');
    const HH = String(d.getHours()).padStart(2, '0');
    const MM = String(d.getMinutes()).padStart(2, '0');
    return `${d.getFullYear()}/${mm}/${dd} ${HH}:${MM}`;
};

// 计算可滚动高度
const calcScrollHeight = () => {
    const inst = getCurrentInstance();
    if (!inst) return;
    uni.createSelectorQuery().in(inst)
        .select('.input-bar')
        .boundingClientRect(rect => {
            if (!rect) return;
            const winH = uni.getSystemInfoSync().windowHeight;
            scrollViewHeight.value = winH - rect.height - keyboardHeight.value;
            scrollToBottom();
        })
        .exec();
};

// 滚动到底部
const scrollToBottom = (force = false) => {
    if (scrollTop.value > 90000 || force) {
        nextTick(() => {
            scrollTop.value = messages.value.length * 200;
        });
    }
};

// 拉取最新一页消息
const fetchInitialMessages = async () => {
    try {
        const res = await uniCloud.callFunction({
            name: 'get-history-messages',
            data: {
                sessionId:    sessionId.value,
                lastTimestamp: Date.now(),
                pageSize:     PAGE_SIZE
            }
        });
        if (res.result.success) {
            const arr = await processMessages(res.result.data);
            messages.value = arr;
            noMoreMessages.value = arr.length < PAGE_SIZE;
            scrollToBottom(true);
        } else {
            uni.showToast({ title: '获取消息失败', icon: 'none' });
        }
    } catch (e) {
        console.error('fetchInitialMessages', e);
    }
};

// 分页加载更早消息
const loadMoreMessages = async () => {
    if (isLoadingMore.value || noMoreMessages.value) return;
    isLoadingMore.value = true;
    try {
        const firstTs = messages.value[0]?.timestamp || Date.now();
        const res = await uniCloud.callFunction({
            name: 'get-history-messages',
            data: {
                sessionId:     sessionId.value,
                lastTimestamp: firstTs,
                pageSize:      PAGE_SIZE
            }
        });
        if (res.result.success && res.result.data.length > 0) {
            const arr = await processMessages(res.result.data);
            messages.value = [...arr, ...messages.value];
            noMoreMessages.value = res.result.data.length < PAGE_SIZE;
        } else {
            noMoreMessages.value = true;
        }
    } catch (e) {
        console.error('loadMoreMessages', e);
    } finally {
        isLoadingMore.value = false;
    }
};

// 处理文件 URL、头像等
const processMessages = async raw => {
    const imgs = raw.filter(m => m.type === 'image' && m.fileUrl.startsWith('cloud://'))
                    .map(m => m.fileUrl);
    let urlMap = {};
    if (imgs.length) {
        const tmp = await uniCloud.getTempFileURL({ fileList: imgs });
        tmp.fileList.forEach(item => {
            urlMap[item.fileID] = item.tempFileURL;
        });
    }
    return raw.map(m => ({
        ...m,
        avatarUrl: m.avatarUrl || DEFAULT_AVATAR,
        fileUrl: m.type === 'image' ? (urlMap[m.fileUrl] || m.fileUrl) : m.fileUrl
    }));
};

// 开始 Watch（小程序端专用）【优化增强版】
const startMessageWatcher = () => {
  console.log('【startMessageWatcher】函数被调用。');
  if (messageWatcher) {
    console.log('【startMessageWatcher】messageWatcher 已存在，跳过创建。');
    return;
  }

  // #ifdef MP-WEIXIN
  const db = uniCloud.database();
  const sid = sessionId.value;

  if (!sid) {
    console.warn('【startMessageWatcher】sessionId 为空，无法创建监听器');
    return;
  }

  console.log('【startMessageWatcher】准备监听 sessionId:', sid);

  messageWatcher = db.collection('ChatMessages')
    .where({
      sessionId: sid
    })
    .orderBy('timestamp', 'asc')
    .watch({
      onChange: async (snapshot) => {
        console.log('【watch:onChange】触发，类型:', snapshot.type);

        if (!snapshot.docChanges || snapshot.docChanges.length === 0) {
          console.log('【watch:onChange】无 docChanges，跳过处理。');
          return;
        }

        const addedDocs = snapshot.docChanges
          .filter(change => change.dataType === 'add')
          .map(change => change.doc);

        if (addedDocs.length === 0) {
          console.log('【watch:onChange】无新增消息，跳过处理。');
          return;
        }

        console.log('【watch:onChange】新增消息数量:', addedDocs.length);
        const formatted = await processMessages(addedDocs);

        formatted.forEach(msg => {
          if (!messages.value.find(m => m._id === msg._id)) {
            messages.value.push(msg);
            console.log('【watch:onChange】添加新消息:', msg._id);
          }
        });

        scrollToBottom(true);
        markMessagesAsRead();
      },

      onError: (err) => {
        console.error('【watch:onError】监听发生错误:', err);
      }
    });

  console.log('【startMessageWatcher】监听器创建完毕 ✅');
  // #endif
};

// 发送消息后也主动拉一次
const triggerRefresh = () => {
    fetchInitialMessages();
};

// 发送文字/图片/位置等
const _sendMessage = async content => {
  const ui = uni.getStorageSync('userinfo') || {};
  const data = {
    type: content.type,
    sender: myPhoneNumber.value,
    avatarUrl: ui.avatar || DEFAULT_AVATAR,
    name: ui.name || '用户',
    sessionId: sessionId.value,
    timestamp: Date.now(),
    isRevoked: false,
    ...(content.type === 'text' ? { message: content.text } : {}),
    ...(content.type === 'image' ? { fileUrl: content.fileID } : {}),
    ...(content.type === 'location' ? { location: content.location } : {})
  };

  try {
    const res = await uniCloud.callFunction({
      name: 'send-chat-message',
      data: { sessionId: sessionId.value, message: data }
    });

    if (res.result.success) {
      messages.value.push({ ...data, _id: res.result.messageId || String(Date.now()) });
      scrollToBottom(true);
    } else {
      uni.showToast({ title: '发送失败', icon: 'none' });
    }
  } catch (e) {
    console.error('sendMessage error:', e);
    uni.showToast({ title: '发送异常', icon: 'none' });
  }
};

// 公共方法：文本／图片／位置
const sendTextMessage = () => {
    if (!messageInput.value.trim()) return;
    _sendMessage({ type: 'text', text: messageInput.value });
    messageInput.value = '';
};
const sendImage = async () => {
    try {
        const res = await uni.chooseImage({ count: 1 });
        const fp  = res.tempFilePaths[0];
        uni.showLoading({ title: '上传中...' });
        const up  = await uniCloud.uploadFile({
            filePath: fp,
            cloudPath: `chat-images/${sessionId.value}/${Date.now()}-${Math.random() * 1000 | 0}`
        });
        uni.hideLoading();
        _sendMessage({ type: 'image', fileID: up.fileID });
    } catch (e) {
        uni.hideLoading();
    }
};

const sendLocation = async () => {
    uni.showLoading({ title: '正在定位...' });
    try {
        const settingRes = await uni.getSetting();
        if (!settingRes.authSetting['scope.userLocation']) {
            await uni.authorize({ scope: 'scope.userLocation' });
        }

        const { latitude, longitude } = await uni.getLocation({
            type: 'gcj02',
            isHighAccuracy: true
        });

        let key = '';
        // #ifdef MP-WEIXIN
        key = QQ_MAP_KEY_MP;
        // #endif
        // #ifdef APP-PLUS
        key = QQ_MAP_KEY_APP;
        // #endif
        
        if (!key) {
            throw new Error('地图Key未配置');
        }

        const geoResponse = await uni.request({
            url: `https://apis.map.qq.com/ws/geocoder/v1/?location=${latitude},${longitude}&key=${key}`
        });

        if (geoResponse.statusCode !== 200 || geoResponse.data.status !== 0) {
            console.error('腾讯地图逆地址解析失败', geoResponse);
            throw new Error(`位置服务异常: ${geoResponse?.data?.message || '网络请求失败'}`);
        }
        
        const r = geoResponse.data.result;

        const loc = {
            latitude,
            longitude,
            name: r.formatted_addresses?.recommend || r.address_component?.street_number || '未知位置',
            address: r.address || '详细地址未知',
            thumbUrl: `https://apis.map.qq.com/ws/staticmap/v2/?center=${latitude},${longitude}&zoom=16&size=800x400&markers=size:large|color:0x3399FF|label:D|${latitude},${longitude}&key=${key}`
        };

        _sendMessage({ type: 'location', location: loc });

    } catch (e) {
        console.error('sendLocation 失败:', e);
        uni.showToast({
            title: e.message || '获取位置失败，请稍后重试',
            icon: 'none',
            duration: 3000
        });
    } finally {
        uni.hideLoading();
    }
};

// 预览图片／位置
const previewImage = url => {
    const urls = messages.value.filter(m => m.type === 'image' && !m.isRevoked).map(m => m.fileUrl);
    uni.previewImage({ current: url, urls });
};
const previewLocation = loc => {
    uni.openLocation({ ...loc });
};

// 标记已读
const markMessagesAsRead = async () => {
    try {
        const res = await uniCloud.callFunction({
            name: 'clear-unread-count',
            data: { sessionId: sessionId.value, userPhoneNumber: myPhoneNumber.value }
        });
        console.log('clear-unread-count result:', res);
        if (!res.result.success) {
            console.error('清除未读消息数失败:', res.result.message);
        }
        return res.result.success;
    } catch (e) {
        console.error('markMessagesAsRead error:', e);
        return false;
    }
};

// 键盘高度变化
uni.onKeyboardHeightChange(res => {
    keyboardHeight.value = res.height;
    calcScrollHeight();
});

// 长按消息处理（撤回功能）
const onMessageLongPress = async (msg) => {
    if (msg.sender !== myPhoneNumber.value) {
        uni.showToast({ title: '只能撤回自己的消息', icon: 'none' });
        return;
    }
    const now = Date.now();
    if (now - msg.timestamp > 2 * 60 * 1000) {
        uni.showToast({ title: '消息发送超过2分钟，无法撤回', icon: 'none' });
        return;
    }
    uni.showActionSheet({
        itemList: ['撤回'],
        success: async (res) => {
            if (res.tapIndex === 0) {
                uni.showLoading({ title: '正在撤回...' });
                try {
                    const result = await uniCloud.callFunction({
                        name: 'revoke-chat-message',
                        data: { messageId: msg._id }
                    });
                    if (result.result.success) {
                        msg.isRevoked = true;
                        uni.showToast({ title: '消息已撤回', icon: 'success' });
                    } else {
                        uni.showToast({ title: result.result.message || '撤回失败', icon: 'none' });
                    }
                } catch (e) {
                    uni.showToast({ title: '撤回失败，请稍后重试', icon: 'none' });
                } finally {
                    uni.hideLoading();
                }
            }
        }
    });
};

// 生命周期函数
onLoad(async options => {
  console.log('【onLoad】页面加载，options:', options); // 在函数最顶部
  if (!options.sessionId) {
    console.warn('【onLoad】缺少 sessionId，将跳转到首页。');
    return uni.switchTab({ url: '/pages/index/index' });
  }
  sessionId.value = options.sessionId;
  console.log('【onLoad】获取到的 sessionId:', sessionId.value);

  const ui = uni.getStorageSync('userinfo');
  if (!ui || !ui.phoneNumber) {
    console.warn('【onLoad】用户未登录或phoneNumber缺失。');
    return uni.showToast({ title: '请先登录', icon: 'none' });
  }
  myPhoneNumber.value = ui.phoneNumber;
  console.log('【onLoad】我的手机号 myPhoneNumber:', myPhoneNumber.value);

  await fetchInitialMessages();
  console.log('【onLoad】初始消息加载完成。');

  await markMessagesAsRead();
  console.log('【onLoad】消息标记已读完成。');

  // #ifdef MP-WEIXIN
  console.log('【onLoad】当前环境为小程序，尝试启动消息监听...');
  startMessageWatcher();
  // #endif
});
onReady(() => {
    calcScrollHeight();
});

onShow(() => {
    // #ifdef APP-PLUS
    pollingTimer = setInterval(fetchInitialMessages, POLL_INTERVAL);
    // #endif
});

onHide(() => {
    // #ifdef APP-PLUS
    clearInterval(pollingTimer);
    pollingTimer = null;
    // #endif
});

onUnmounted(() => {
  console.log('【onUnmounted】页面即将卸载。');
  if (messageWatcher) {
    messageWatcher.close();
    console.log('【onUnmounted】messageWatcher 已关闭。');
  }
  uni.offKeyboardHeightChange();
  console.log('【onUnmounted】键盘高度监听已关闭。');
});
</script>

<style lang="scss">
page {
  height: 100%;
}
.chat-container {
  position: relative;
  height: 100vh;
  background-color: #f5f5f5;
  box-sizing: border-box;
  overflow: hidden;
}

.message-scroll-view {
  box-sizing: border-box;
  padding-bottom: 20rpx;
}

.loading-more {
  text-align: center;
  color: #999;
  font-size: 24rpx;
  padding: 20rpx 0;
}

.message-item {
  display: flex;
  margin: 20rpx 30rpx 40rpx;
  align-items: flex-start;

  &.mine {
    flex-direction: row-reverse;
    .msg-content {
      align-items: flex-end;
    }
    .bubble {
      background: #fff;
    }
  }

  &.system-message {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 30rpx 30rpx 50rpx;

    .avatar {
      display: none;
    }

    .system-icon {
      width: 80rpx;
      height: 80rpx;
      margin-bottom: 20rpx;
      filter: brightness(1.2);
    }

    .bubble {
      background: #FFF8F2;
      color: #E65100;
      text-align: center;
      padding: 28rpx 40rpx;
      border-radius: 16rpx;
      font-size: 34rpx;
      font-weight: 500;
      box-shadow: 0 6rpx 24rpx rgba(230, 81, 0, 0.12);
      border: 1px solid rgba(230, 81, 0, 0.1);
      max-width: 90%;
      line-height: 1.5;
      transition: all 0.3s ease;
    }
  }

  &.appointment_created .bubble {
    background: #E6F7FF; /* 淡蓝色背景 */
    color: #007AFF;
    border: 1px solid #91D5FF;
  }

  &.appointment_cancelled .bubble {
    background: #FFF1F0; /* 浅红色背景 */
    color: #d71100;
    border: 1px solid #FFA39E;
  }
}

.time {
  font-size: 20rpx;
  color: #999;
  margin-top: 12rpx;
  text-align: right;
}

.avatar {
  width: 80rpx;
  height: 80rpx;
  border-radius: 12rpx;
  margin: 0 20rpx;
  flex-shrink: 0;
  background-color: #eee;
}

.msg-content {
  display: flex;
  flex-direction: column;
  max-width: calc(100% - 120rpx - 60rpx);
}

.bubble {
  background: #fff;
  padding: 20rpx 24rpx;
  border-radius: 16rpx;
  font-size: 30rpx;
  line-height: 1.5;
  word-break: break-all;
  box-shadow: 0 2rpx 8rpx rgba(0,0,0,0.05);
}

.msg-image {
  max-width: 450rpx;
  max-height: 400rpx;
  border-radius: 12rpx;
  display: block;
}

.location-msg {
  width: 480rpx;
  border-radius: 8rpx;
  overflow: hidden;
  .map-thumb {
    width: 100%;
    height: 280rpx;
    display: block;
  }
  .map-info {
    padding: 15rpx 20rpx;
    background-color: #fff;
  }
  .map-title {
    font-size: 28rpx;
    font-weight: bold;
    color: #333;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 8rpx;
  }
  .map-address {
    font-size: 24rpx;
    color: #777;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.revoked-text {
  font-size: 24rpx;
  color: #999;
}

.input-bar {
  position: fixed;
  left: 0;
