  display: flex;
  align-items: center;
  padding: 15rpx 20rpx;
  padding-bottom: calc(15rpx + env(safe-area-inset-bottom));
  background: #f8f8f8;
  border-top: 1rpx solid #e0e0e0;
  transition: bottom 0.2s ease-out;
  z-index: 100;
  box-sizing: border-box;

  .icon-btn {
    width: 56rpx;
    height: 56rpx;
    margin: 0 10rpx;
    flex-shrink: 0;
  }

  .input {
    flex: 1;
    height: 76rpx;
    padding: 0 28rpx;
    background: #fff;
    border-radius: 40rpx;
    font-size: 28rpx;
  }

  .send-btn {
    height: 72rpx;
    padding: 0 36rpx;
    line-height: 72rpx;
    background: #000000;
    color: white;
    border-radius: 40rpx;
    font-size: 28rpx;
    margin-left: 10rpx;
    flex-shrink: 0;
  }
}
</style>